<template>
  <div class="professionlist">
    <div class="search">
      <div class="Keyword">
        <span>关 键 字 ：</span>
        <input type="text" placeholder="请输入职位关键词：IT修真院" v-model="name" />
      </div>

      <div class="row">
        <span>所在地区：</span>
        <div
          v-for="(item,key) in region"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,region)"
        >{{item.option}}</div>
      </div>

      <div class="row">
        <span>职业类别：</span>
        <div
          v-for="(item,key) in category"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,category)"
        >{{item.option}}</div>
      </div>

      <div class="row mar" v-show="subType1">
        <div
          v-for="(item,key) in canpin"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,canpin)"
        >{{item.option}}</div>
      </div>
      <div class="row mar" v-show="subType2">
        <div
          v-for="(item,key) in javaui"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,javaui)"
        >{{item.option}}</div>
      </div>
      <div class="row mar" v-show="subType3">
        <div
          v-for="(item,key) in qita"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,qita)"
        >{{item.option}}</div>
      </div>

      <div class="row">
        <span>所属行业：</span>
        <div
          v-for="(item,key) in industry"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,industry)"
        >{{item.option}}</div>
      </div>

      <div class="row">
        <span>学历要求：</span>
        <div
          v-for="(item,key) in education"
          @click="dianji(key,education)"
          v-bind:class="{'color': item.Choice}"
        >
          <!-- <input type="checkbox" v-model="item.Choice"> -->
          {{item.option}}
        </div>
      </div>

      <div class="row">
        <span>工作经验：</span>
        <div
          v-for="(item,key) in experience"
          @click="dianji(key,experience)"
          v-bind:class="{'color': item.Choice}"
        >
          <!-- <input type="checkbox" v-model="item.Choice"> -->
          {{item.option}}
        </div>
      </div>

      <div class="row">
        <span>薪资水平：</span>
        <div
          v-for="(item,key) in salary"
          @click="dianji(key,salary)"
          v-bind:class="{'color': item.Choice}"
        >
          <!-- <input type="checkbox" v-model="item.Choice"> -->
          {{item.option}}
        </div>
      </div>

      <div class="but">
        <button @click="empty">清空</button>
        <button @click="search">搜索</button>
      </div>
    </div>

    <div class="list" v-if="!fourzerofour">
      <div class="job" v-for="(item,key) in response.data" @click="zhiye(item.id)">
        <div class="job_left">
          <div class="job_left_row">
            <h5>{{item.name}}</h5>
            <span>发布时间 ：</span>
            <span>{{item.updateAt|formatDate}}</span>
          </div>
          <div class="job_left_row">
            <div class="wages" v-if="item.compensation==0">8k以下</div>
            <div class="wages" v-if="item.compensation==1">8-10k</div>
            <div class="wages" v-if="item.compensation==2">10-15k</div>
            <div class="wages" v-if="item.compensation==3">15-20k</div>
            <div class="wages" v-if="item.compensation==4">20k以上</div>
          </div>
          <div class="job_left_row">
            <div class="place">
              <span>北京市-朝阳区</span>
              <span v-if="item.education==0">/ 高中 /</span>
              <span v-if="item.education==1">/ 大专 /</span>
              <span v-if="item.education==2">/ 本科 /</span>
              <span v-if="item.education==3">/ 硕士 /</span>
              <span v-if="item.education==4">/ 博士 /</span>
              <span v-if="item.experience==0">无</span>
              <span v-if="item.experience==1">应届</span>
              <span v-if="item.experience==2">0-1年</span>
              <span v-if="item.experience==3">1-2年</span>
              <span v-if="item.experience==4">3-5年</span>
              <span v-if="item.experience==5">6-9年</span>
              <span v-if="item.experience==6">10年以上</span>
            </div>
          </div>
        </div>
        <div class="job_right">
          <div>{{item.companyName}}</div>
          <div class="industry">
            <span v-for="ite in item.industryList">{{ite.industry|industry}}</span>
          </div>
          <div class="img">
            <img :src="item.logo" alt />
          </div>
        </div>
      </div>

      <div class="page">
        <el-pagination
          background
          :current-page.sync="currentPage1"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total="response.total"
          style="white-space:normal"
        ></el-pagination>
      </div>
    </div>

    <div class="fourzerofour" v-if="fourzerofour">
      <div class="fourzerofour_row1">
        <img src="../assets/404.png" alt style="width:150px" />
        <div class="tips">
          <h4>暂时没有符合该搜索条件的内容</h4>
          <p>请重新修改搜索条件后再次进行搜索</p>
        </div>
      </div>
      <h5 style="margin:15px 0 0">推荐职位</h5>
      <div class="fourzerofour_row2">
        <el-row :gutter="30">
          <el-col :span="8" v-for="item in tuijian" >
            <div class="grid-content bg-purple" @click="zhiye(item.id)">
              <div class="img">
                <img :src="item.logo" alt />
              </div>
              <div class="text">
                <p v-if="item.compensation ==0" class="p1">8k以下</p>
                <p v-if="item.compensation ==1" class="p1">8~10K</p>
                <p v-if="item.compensation ==2" class="p1">10~15K</p>
                <p v-if="item.compensation ==3" class="p1">15-20K</p>
                <p v-if="item.compensation ==4" class="p1">20K以上</p>
                <p class="p2">{{item.name}}</p>
                <p class="p3">{{item.companyName}}</p>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      region: [
        { option: "不限", Choice: true },
        { option: "北京", Choice: false }
      ],
      industry: [
        { option: "不限", Choice: true },
        { option: "移动互联网", Choice: false },
        { option: "电子商务", Choice: false },
        { option: "企业服务", Choice: false },
        { option: "O2O", Choice: false },
        { option: "教育", Choice: false },
        { option: "金融", Choice: false },
        { option: "游戏", Choice: false }
      ],
      category: [
        { option: "不限", Choice: true },
        { option: "产品", Choice: false },
        { option: "UI", Choice: false },
        { option: "QA", Choice: false },
        { option: "Android", Choice: false },
        { option: "IOS", Choice: false },
        { option: "WEB", Choice: false },
        { option: "OP", Choice: false },
        { option: "JAVA", Choice: false },
        { option: "NLP", Choice: false },
        { option: "DW", Choice: false },
        { option: "DL", Choice: false }
      ],
      canpin: [
        { option: "不限", Choice: true },
        { option: "助理", Choice: false },
        { option: "初级", Choice: false },
        { option: "中级", Choice: false },
        { option: "高级", Choice: false },
        { option: "总监", Choice: false }
      ],
      javaui: [
        { option: "不限", Choice: true },
        { option: "初级", Choice: false },
        { option: "中级", Choice: false },
        { option: "高级", Choice: false },
        { option: "总监", Choice: false }
      ],
      qita: [
        { option: "不限", Choice: true },
        { option: "初级", Choice: false },
        { option: "中级", Choice: false },
        { option: "高级", Choice: false }
      ],
      education: [
        { option: "不限", Choice: true },
        { option: "高中", Choice: false },
        { option: "大专", Choice: false },
        { option: "本科", Choice: false },
        { option: "硕士", Choice: false },
        { option: "博士及以上", Choice: false }
      ],
      experience: [
        { option: "不限", Choice: true },
        { option: "无", Choice: false },
        { option: "应届", Choice: false },
        { option: "0-1年", Choice: false },
        { option: "1-2年", Choice: false },
        { option: "3-5年", Choice: false },
        { option: "6-9年", Choice: false },
        { option: "10年以上", Choice: false }
      ],
      salary: [
        { option: "不限", Choice: true },
        { option: "8k以下", Choice: false },
        { option: "8-10k", Choice: false },
        { option: "10-15k", Choice: false },
        { option: "15-20k", Choice: false },
        { option: "20k以上", Choice: false }
      ],
      name: "",
      fourzerofour: false,
      currentPage1: 1,
      subType1: false,
      subType2: false,
      subType3: false,
      api: "/api/a/profession/search?",
      response: [],
      tuijian: null
    };
  },
  filters: {
    formatDate: function(value) {
      let date = new Date(value);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
    },
    industry: function(key) {
      let o;
      switch (key) {
        case 0:
          o = "移动互联网";

          break;
        case 1:
          o = "电子商务";

          break;
        case 2:
          o = "企业服务";

          break;
        case 3:
          o = "O2O";

          break;
        case 4:
          o = "教育";

          break;
        case 5:
          o = "金融";

          break;
        case 6:
          o = "游戏";

          break;
      }
      return o;
    }
  },

  created() {
    if (this.$route.query.category) {
      this.api += "category=" + this.$route.query.category + "&";
      this.category[this.$route.query.category].Choice = true;
      this.category[0].Choice = false;
      if (this.$route.query.subCategory) {
        this.api += "subCategory=" + this.$route.query.subCategory + "&";
        switch (Number(this.$route.query.category) ) {
          case 1:
            this.canpin[this.$route.query.subCategory].Choice = true;
            this.canpin[0].Choice = false;
            console.log(this.$route.query.category);
            break;

          case 2:
            this.javaui[this.$route.query.subCategory].Choice = true;
            this.javaui[0].Choice = false;
            console.log(this.$route.query.category);
            break;

          case 8:
            this.javaui[this.$route.query.subCategory].Choice = true;
            this.javaui[0].Choice = false;
            console.log(this.$route.query.category);
            break;

          default:
            this.qita[this.$route.query.subCategory].Choice = true;
            this.qita[0].Choice = false;
            console.log(this.$route.query.category);
            break;
        }
      }
      sessionStorage.clear();
    }
    this.api += "page=";
    console.log(this.api);
    if (sessionStorage.getItem("api") != null) {

    if (sessionStorage.getItem("api").indexOf('company') != -1) {
         sessionStorage.clear();
        }else{
           this.api = sessionStorage.getItem("api");
        }
    }
    if (sessionStorage.getItem("currentPage1") != null) {
      this.currentPage1 = Number(sessionStorage.getItem("currentPage1"));
    }
    if (sessionStorage.getItem("name") != null) {
      this.name = sessionStorage.getItem("name");
    }
    if (sessionStorage.getItem("region") != null) {
      this.region = JSON.parse(sessionStorage.getItem("region"));
    }
    if (sessionStorage.getItem("industry") != null) {
      this.industry = JSON.parse(sessionStorage.getItem("industry"));
    }
    if (sessionStorage.getItem("education") != null) {
      this.education = JSON.parse(sessionStorage.getItem("education"));
    }
    if (sessionStorage.getItem("experience") != null) {
      this.experience = JSON.parse(sessionStorage.getItem("experience"));
    }
    if (sessionStorage.getItem("salary") != null) {
      this.salary = JSON.parse(sessionStorage.getItem("salary"));
    }
    if (sessionStorage.getItem("category") != null) {
      this.category = JSON.parse(sessionStorage.getItem("category"));
    }
    if (sessionStorage.getItem("canpin") != null) {
      this.canpin = JSON.parse(sessionStorage.getItem("canpin"));
    }
    if (sessionStorage.getItem("javaui") != null) {
      this.javaui = JSON.parse(sessionStorage.getItem("javaui"));
    }
    if (sessionStorage.getItem("qita") != null) {
      this.qita = JSON.parse(sessionStorage.getItem("qita"));
    }
    sessionStorage.setItem("api", this.api);
    console.log(this.api);
    axios.get(this.api + this.currentPage1).then(response => {
      this.response = response.data;
      console.log(this.response);
    });
  },
  methods: {
    //  搜索
    search() {
      let api = "/api/a/profession/search?";
      //  搜索关键字
      if (this.name) {
        api += `name=${this.name}&`;
        sessionStorage.setItem("name", this.name);
      }
      // 地区搜索
      for (let i = 1; i < this.region.length; i++) {
        if (this.region[i].Choice == true) {
          api = api + "city=" + i + "&";
        }
      }
      sessionStorage.setItem("region", JSON.stringify(this.region));
      // 行业搜索
      for (let i = 1; i < this.industry.length; i++) {
        if (this.industry[i].Choice == true) {
          api = api + "industry=" + (i - 1) + "&";
        }
      }
      sessionStorage.setItem("industry", JSON.stringify(this.industry));
      // 学历搜索
      for (let i = 1; i < this.education.length; i++) {
        if (this.education[i].Choice == true) {
          api = api + "education=" + (i - 1) + "&";
        }
      }
      sessionStorage.setItem("education", JSON.stringify(this.education));
      // 经验搜索
      for (let i = 1; i < this.experience.length; i++) {
        if (this.experience[i].Choice == true) {
          api = api + "experience=" + (i - 1) + "&";
        }
      }
      sessionStorage.setItem("experience", JSON.stringify(this.experience));
      //  薪资搜索
      for (let i = 1; i < this.salary.length; i++) {
        if (this.salary[i].Choice == true) {
          api = api + "compensation=" + (i - 1) + "&";
        }
      }
      sessionStorage.setItem("salary", JSON.stringify(this.salary));
      // 职业搜索
      for (let i = 1; i < this.category.length; i++) {
        if (this.category[i].Choice == true) {
          api = api + "category=" + i + "&";
        }
      }
      sessionStorage.setItem("category", JSON.stringify(this.category));
      // 产品等级搜索
      if (this.subType1) {
        for (let i = 1; i < this.canpin.length; i++) {
          if (this.canpin[i].Choice == true) {
            api = api + "subCategory=" + i + "&";
          }
        }
        sessionStorage.setItem("canpin", JSON.stringify(this.canpin));
      }
      // java ui等级搜索
      if (this.subType2) {
        for (let i = 1; i < this.javaui.length; i++) {
          if (this.javaui[i].Choice == true) {
            api = api + "subCategory=" + i + "&";
          }
        }
        sessionStorage.setItem("javaui", JSON.stringify(this.javaui));
      }
      // 其他等级搜索
      if (this.subType3) {
        for (let i = 1; i < this.qita.length; i++) {
          if (this.qita[i].Choice == true) {
            api = api + "subCategory=" + i + "&";
          }
        }
        sessionStorage.setItem("qita", JSON.stringify(this.qita));
      }
      api = api + "page=";
      this.currentPage1 = 1;
      sessionStorage.setItem("api", api);
      axios.get(api + this.currentPage1).then(response => {
        this.response = response.data;
        this.fourzerofour = false;
        this.$router.push({ path: "/searchmain/professionlist" });
      });
    },

    //  清空
    empty() {
      sessionStorage.clear();
      // 搜索栏
      this.name = "";
      //  地区
      for (let i = 1; i < this.region.length; i++) {
        this.region[i].Choice = false;
      }
      this.region[0].Choice = true;
      // 行业
      for (let i = 1; i < this.industry.length; i++) {
        this.industry[i].Choice = false;
      }
      this.industry[0].Choice = true;
      //  学历
      for (let i = 1; i < this.education.length; i++) {
        this.education[i].Choice = false;
      }
      this.education[0].Choice = true;
      //  经验
      for (let i = 1; i < this.experience.length; i++) {
        this.experience[i].Choice = false;
      }
      this.experience[0].Choice = true;
      //  薪资
      for (let i = 1; i < this.salary.length; i++) {
        this.salary[i].Choice = false;
      }
      this.salary[0].Choice = true;
      //  职业
      for (let i = 1; i < this.category.length; i++) {
        this.category[i].Choice = false;
      }
      this.category[0].Choice = true;
      //  产品等级
      for (let i = 1; i < this.canpin.length; i++) {
        this.canpin[i].Choice = false;
      }
      this.canpin[0].Choice = true;
      //  javaui等级
      for (let i = 1; i < this.javaui.length; i++) {
        this.javaui[i].Choice = false;
      }
      this.javaui[0].Choice = true;
      //  其他等级
      for (let i = 1; i < this.qita.length; i++) {
        this.qita[i].Choice = false;
      }
      this.qita[0].Choice = true;
      this.subType1 = false;
      this.subType2 = false;
      this.subType3 = false;
      this.api = "/api/a/profession/search?page=";
      this.currentPage1 = 1;
      sessionStorage.setItem("api", this.api);
      sessionStorage.setItem("currentPage1", this.currentPage1);
      console.log(this.api);
      axios.get(this.api + this.currentPage1).then(response => {
        this.response = response.data;
        this.fourzerofour = false;
        this.$router.push({ path: "/searchmain/professionlist" });
      });
    },
    dianji(index, array) {
      this.$service.search_criteria(index, array);
    },
    zhiye(id){
      this.$router.push({ path: `/jobdetails?id=${id}` });
    },
    
  },
  watch: {
    category: {
      handler(newvalue, oldvalue) {
        let y = [];
        for (let i = 1; i < this.category.length; i++) {
          if (this.category[i].Choice == true) {
            y.push(i);
          }
        }
        if (y.length == 1) {
          switch (y[0]) {
            case 1:
              this.subType1 = true;
              break;

            case 2:
            case 8:
              this.subType2 = true;
              break;
            default:
              this.subType3 = true;
              break;
          }
        } else {
          this.subType1 = false;
          this.subType2 = false;
          this.subType3 = false;
        }
      },
      deep: true
    },
    currentPage1: function(newvalue, oldvalue) {
      this.api = sessionStorage.getItem("api");
      axios.get(this.api + this.currentPage1).then(response => {
        this.response = response.data;
        // sessionStorage.setItem("api", this.api)
        sessionStorage.setItem("currentPage1", this.currentPage1);
      });
    },

    response: function(newvalue, oldvalue) {
      if (this.response.data) {
        if (this.response.data.length == 0) {
          axios.get("/api/a/profession/search?size=3").then(response => {
            this.tuijian = response.data.data;
            this.fourzerofour = true;
          });
        }
      }
    }
  }
};
</script>
<style scoped lang="scss">
.search {
  padding: 10px 15px 20px;
  background: white;
  .Keyword {
    width: 100%;
    font-size: 11px;
    font-weight: 900;
    margin-bottom: 15px;
    input {
      width: 350px;
      height: 12px;
      padding: 6px 12px;
      font-size: 12px;
      line-height: 1.42857143;
      color: #555;
      background-color: #fff;
      background-image: none;
      border: 1px solid #ccc;
      border-radius: 4px;
      -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
      outline: none;
    }
    input::-webkit-input-placeholder {
      color: rgba(206, 206, 206, 0.8);
    }
  }
  .row {
    width: 100%;
    font-size: 11px;
    font-weight: 900;
    margin-bottom: 15px;
    div {
      display: inline-block;
      font-size: 11px;
      font-weight: 400;
      padding: 3px 8px;
      text-align: center;
      margin: 0 3px;
      cursor: pointer;
    }
  }
  .mar {
    margin-left: 55px;
  }
  .but {
    text-align: right;
    button {
      width: 50px;
      height: 20px;
      margin-left: 10px;
      background: rgb(252, 43, 71);
      color: white;
      font-size: 10px;
      border: 1px solid rgb(252, 43, 71);
      outline: none;
      cursor: pointer;
    }
  }
  .color {
    background: rgb(252, 43, 71);
    color: white;
  }
}
.list {
  padding: 10px 15px 20px;
  background: white;
  margin-top: 20px;
  position: relative;
  .job {
    display: flex;
    padding: 8px 25px;
    border: 1px solid #ebebeb;
    border-radius: 2px;
    margin-bottom: 20px;
    cursor: pointer;
    .job_left {
      width: 50%;
      border-right: 1px solid #ebebeb;
      .job_left_row {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: 30px;
        h5 {
          margin-right: 15px;
        }
        span {
          color: #495c60;
