<template>
  <div class="company">
   <div class="main" >
       <div class="header" v-if="comany">
           <div class="img"><img :src="comany.data.company.logo" alt=""></div>
           
           <div class="header_right">
               <h5>{{comany.data.company.name}}</h5>
               <span>{{comany.data.company.slogan}}</span>
           </div>
       </div>
       <div class="details">
        <div class="details_left">
            <div class="but">
                <div v-bind:class="{'color':qiehuan}" @click="qiehuan1">公司详情</div>
                <div v-bind:class="{'color':!qiehuan}"@click="qiehuan2">再招职位</div>
            </div>
            <div class="introduce">
                <h5 v-show="qiehuan">  <div></div>公司介绍</h5>
                <h5 v-show="!qiehuan">  <div></div>再招职位</h5>
                <p v-bind:class="{'qiehuan':!qiehuan}" v-for="item in introduction">{{item}}</p>
                     <ul v-bind:class="{'qiehuan':qiehuan}" >
                     <li v-for="item in job" @click="joblis(item.id)">
                         <div class="position">{{item.name}} [北京]</div>
                         <div class="wages" v-if="item.compensation==0">8k以下</div>
                         <div class="wages" v-if="item.compensation==1">8-10k</div>
                         <div class="wages" v-if="item.compensation==2">10-15k</div>
                         <div class="wages" v-if="item.compensation==3">15-20k</div>
                         <div class="wages" v-if="item.compensation==4">20k以上</div>
                         <div class="experience">
                             <span v-if="item.experience==0">无</span>
                             <span v-if="item.experience==1">应届</span>
                             <span v-if="item.experience==2">0-1年</span>
                             <span v-if="item.experience==3">1-2年</span>
                             <span v-if="item.experience==4">3-5年</span>
                             <span v-if="item.experience==5">6-9年</span>
                             <span v-if="item.experience==6">10年以上</span>

                             <span v-if="item.education==0">高中</span>
                             <span v-if="item.education==1">大专</span>
                             <span v-if="item.education==2">本科</span>
                             <span v-if="item.education==3">硕士</span>
                             <span v-if="item.education==4">博士及以上</span>
                         </div>
                         <div class="time">{{item.updateAt|formatDate}}发布</div>
                     </li>
                 </ul>
        <div class="page" v-bind:class="{'qiehuan':qiehuan}"> 
           <el-pagination
           background
           :current-page.sync="currentPage1"
           :page-size="10"
           layout="prev, pager, next, jumper"
           :total="10">
           </el-pagination>
         </div>  
        </div>
        </div>
        <div class="details_right"  v-if="comany">
            <div class="information">
             <span>公司信息</span>
             <div></div>
            </div>
            <div class="xiangqing">
                <i class="el-icon-user"></i> 
                <span v-if="comany.data.company.totalNum==0">30-50人</span>
                <span v-if="comany.data.company.totalNum==1">30-50人</span>
                <span v-if="comany.data.company.totalNum==2">30-50人</span>
                <span v-if="comany.data.company.totalNum==3">50-100人</span>
                <span v-if="comany.data.company.totalNum==4">100-200人</span>
                <span v-if="comany.data.company.totalNum==5">200-500人</span>
                <span v-if="comany.data.company.totalNum==6">500-1000人</span>
                <span v-if="comany.data.company.totalNum==7">1000人以上</span>
                </div>
            <div class="xiangqing">
                <i class="el-icon-mobile"></i> 
                <span v-for="item in comany.data.industryList">
                   {{item.industry|industry}}
                    </span>
                </div>
            <div class="xiangqing"><i class="el-icon-location-outline"></i> <span>北京市</span></div>
            <div class="information" style="margin-top:50px;">
             <span>公司标签</span>
             <div></div>
            </div>
            <div class="lable">
                <div v-for="item in companyLable">{{item|companyLable}}</div>
            </div>
        </div>
       </div>
   </div>
    
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name:'nampany',
  data(){
   return{
  qiehuan:true,
  currentPage1:1,
  comany:null,
  companyLable:null,
  job:null,
  introduction:null,
    }
},
filters: {
      formatDate: function (value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m;
      },
      industry:function(key){
        let o;
        switch(key){
             case 0:
             o = '移动互联网';
           
             break;
             case 1:
             o = '电子商务';
         
             break;
             case 2:
             o = '企业服务';
         
             break;
             case 3:
             o = 'O2O';
          
             break;
             case 4:
             o = '教育';
           
             break;
             case 5:
             o = '金融';
        
             break;
             case 6:
             o = '游戏';
         
             break
             
        }
        return o
      },
       companyLable:function(key){
        let o;
        switch(key){
             case 0:
             o = '管理扁平化';
           
             break;
             case 1:
             o = '发展前景好';
         
             break;
             case 2:
             o = '成长空间大';
         
             break;
             case 3:
             o = '五险一金';
          
             break;
             case 4:
             o = '餐补';
           
             break;
             case 5:
             o = '系统培训';
        
             break;
             case 6:
             o = '';
         
             break
             case 7:
             o = '餐补';
         
             break
               case 8:
             o = '';
         
             break
               case 9:
             o = '';
         
             break
               case 10:
             o = '高效自由';
         
             break
               case 11:
             o = '氛围好';
         
             break
              case 12:
             o = '大牛带领';
         
             break
               case 13:
             o = '弹性工作';
         
             break
                case 14:
             o = '带薪假期';
         
             break
              case 15:
             o = '平台广阔';
             break
               case 16:
             o = '免费零食';
             break
        }
        return o
      }
    },
created(){
   
   
    if(sessionStorage.getItem("qiehuan")=='true'){
      this.qiehuan=true
    }else if(sessionStorage.getItem("qiehuan")=='false'){
      this.qiehuan=false
    }
    console.log(this.qiehuan)
    console.log(this.$route.query.id)
    axios.get(`/api/a/company/${this.$route.query.id}`).then((response)=>{
        this.comany=response.data
        this.companyLable=JSON.parse(this.comany.data.company.companyLable)
        this.introduction=response.data.data.company.summary.replace(/\n/g,"<br/>")
        this.introduction=this.introduction.split("<br/>")
        for(let i=0;i<this.introduction.length;i++){
            if(this.introduction[i]==""){
                this.introduction.splice(i,1)
                 i=i-1
            }
           
        }

    })

    axios.get(`/api/a/profession/search?companyId=${this.$route.query.id}`).then((response)=>{

this.job=response.data.data

    })
},
methods:{
    qiehuan1(){
        this.qiehuan=true
        sessionStorage.setItem("qiehuan", this.qiehuan)
    },
   qiehuan2(){
        this.qiehuan=false
        sessionStorage.setItem("qiehuan", this.qiehuan)
    },
    joblis(id){
        this.$router.push({path:`jobdetails?id=${id}`})
    }
   },
  }



</script>
<style scoped lang="scss">
 .company{
     .main{
     width:950px;
     margin:20px auto;
     .header{
         width:100%;
         height: 160px;
         background: white;
         display: flex;
         .img{
             width:140px;
             border:1px solid rgba(0,0,0,.075);
             padding:10px;
             display: flex;
             align-items: center;
              img{
             width:100%
         }
         }
        
         .header_right{
             margin-left: 20px;
             h5{
                 margin:40px 0 15px;
             }
             span{
                 font-size: 11px;
                 color: rgb(73,92,96);
             }
         }
     }
     .details{
         margin-top:30px;
         width:100%;
         padding:0 0 30px 10px;
         display: flex;
         background: white;
         box-sizing: border-box;
         .details_left{
          width:70%;
         .but{
          border-bottom: 1px solid rgba(0,0,0,.075);
          margin-right:20px;
          div{
              display: inline-block;
              font-size: 13px;
              padding:10px;
              margin-bottom: -1px;
              cursor: pointer;
          }
         .color{
           border-bottom: 1.5px solid red;
           color:red;
           }
         }
         .introduce{
            padding-right:17px;
            border-right: 2px solid rgba(0,0,0,.075);
            padding-bottom: 30px;
            height: 90%;
            box-sizing: border-box;
            position: relative;
            h5{
                padding:20px 0 30px 10px;
                div{
                    display:inline-block;
                    width:4px;
                    height:15px;
                    margin-bottom: -3px;
                    margin-right: 10px;
                    background: red;
                }
            }
            p{
                font-size: 12px;
                line-height: 18px;
                color:rgb(73,92,96);
                margin-left: 18px;
                margin-bottom: 20px;
                word-spacing: 5px;
                //  display: none;
            }
            ul{
               list-style:none;
               li{
                   display:flex;
                   padding:18px 20px;
                   align-items: center;
                   border:1px solid rgba(0,0,0,.075);
                   margin-bottom: 20px;
                   cursor: pointer;
                   .position{
                       width: 25%;
                       font-size: 13px;
                       font-weight: 900;
                       white-space: nowrap;
                       text-overflow: ellipsis;
                       overflow: hidden;
                       text-align: left;
                       box-sizing: border-box;
                       padding-right:20px;
                   }
                   .wages{
                       width: 25%;
                       font-size: 12px;
                       color:red;
                       text-align: left;
                       box-sizing: border-box;
                        padding: 0 30px;
                   }
                   .experience{
                       width: 25%;
                       font-size: 12px;
                       color: rgb(73,92,96);
                       text-align: left;
                       box-sizing: border-box;
                   }
                   .time{
                       width: 25%;
                       font-size: 11px;
                       color:rgb(167,167,167);
                       text-align: left;
                       box-sizing: border-box;
                   }

               }
            }
           
            .qiehuan{
                display:none;
            }
            .page{
               height: 30px;
               margin-bottom:  10px;
            //    display: flex;
            //    justify-content: flex-end;
            //    align-items: center;
               position: absolute;
               right: 20px;
               
                 }
         }
          
       }
       .details_right{
        padding: 30px 30px 50px;
        width:28%;
        min-height: 370px;
        .information{
            font-size: 12px;
            font-weight: 900;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 40px;
            span{
                color:rgb(83,106,130)
            }
            div{
                width:65%;
                height:1px;
                margin-left: 30px;
                background: rgb(232,232,232);
            }
        }
        .lable{
            padding-left:20px; 
            div{
                display: inline-block;
                font-size: 8px;
                border:1px solid rgb(217,217,217);
                padding:1px 8px;
                border-radius:25px;
                margin:0 15px 10px 0;
                color:rgb(73,86,110)
            }

        }
        .xiangqing{
            font-size: 11px;
            margin: 0px  0 25px 30px;
           
            i{
                margin-right: 15px;
                font-size: 15px;
                margin-bottom: -30px;
                 color:rgb(83,106,130)
            }
            span{
                font-size: 10px;
                margin-right: 3px;
                 color:rgb(83,106,130)
            }
        }

       }
     }
    } 
@media (max-width: 960px) {
  .main{
    width:768px;
 .details{
     .details_right{
        .information{
            div{
                margin-left: 10px;
            }
         }
       }
     }
  }
}
@media (max-width: 768px) {
  .main{
    width:100%;
 .details{
     flex-wrap: wrap;
     .details_left{
      width:100%;
       .introduce{
           border-right: none;
           margin-bottom: 20px;
       }
     }
     .details_right{
        width:100%;
        .information{
            justify-content: flex-start;
         }
       }
     }
  }
}

 }


</style>