import Vue from 'vue'
import VueRouter from 'vue-router'
import Navigation from '../Navigation.vue'
import Home from '../components/Home.vue'
import About from '../components/About.vue'
import Findjob from '../components/Findjob.vue'
import Jobdetails from '../components/Jobdetails.vue'
import Joblist from '../components/Joblist.vue'
import Findelite from '../components/Findelite.vue'
import Company from '../components/Company.vue'
import SearchMain from '../components/SearchMain.vue'
import CompanyList from '../components/CompanyList.vue'
import ProfessionList from '../components/ProfessionList.vue'
Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
};


const routes = [
  {
    path: '/navigation',
    name: 'navigation',
    component: Navigation
  },
  {
    path: '/foot',
    name: 'foot',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../Foot.vue')
  },
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path:'*',
    redirect:'/home'
  },
  {
    path: '/about',
    name: 'about',
    component: About
  },{
    path: '/findjob',
    name: 'findjob',
    component: Findjob
  },{
    path: '/jobdetails',
    name: 'jobdetails',
    component: Jobdetails
  },{
    path: '/joblist',
    name: 'joblist',
    component: Joblist
  },{
    path: '/findelite',
    name: 'findelite',
    component: Findelite
  },{
    path: '/company',
    name: 'company',
    component: Company
  },{
    path: '/searchmain',
    name: 'searchmain',
    component: SearchMain,
    children:[{path:'companylist',component:CompanyList},
    {path:'professionlist',component:ProfessionList}]
  },
]

const router = new VueRouter({
  routes
})

export default router
