<template>
  <div class="companylist">
    <div class="search">
      <div class="Keyword">
        <span>关 键 字 ：</span>
        <input type="text" placeholder="请输入职位关键词：IT修真院" v-model="name" />
      </div>

      <div class="row">
        <span>所在地区：</span>
        <div
          v-for="(item,key) in region"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,region)"
        >{{item.option}}</div>
      </div>

      <div class="row">
        <span>所属行业：</span>
        <div
          v-for="(item,key) in industry"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,industry)"
        >{{item.option}}</div>
      </div>

      <div class="row">
        <span>融资规模：</span>
        <div
          v-for="(item,key) in financing"
          v-bind:class="{'color': item.Choice}"
          @click="dianji(key,financing)"
        >{{item.option}}</div>
      </div>

      <div class="but">
        <button @click="empty">清空</button>
        <button @click="search">搜索</button>
      </div>
    </div>

    <div class="main" v-show="!fourzerofour">
      <div class="company" v-for="item in response.data " @click="gslb(item.id)">
        <div class="companyintroduce">
          <div class="img">
            <img :src="item.logo" alt />
          </div>
          <div class="text">
            <p class="p1">{{item.name}}</p>
            <p class="p2">{{item.slogan}}</p>
            <p class="p3">
              <span>{{item.professionNum}}</span>个再招职位
            </p>
          </div>
        </div>
        <div class="companyindustry">
          <div class="icio">
            <i class="el-icon-mobile-phone"></i>
            <span v-for="ite in item.industryList">{{ite|industry}}</span>
          </div>
          <div class="icio">
            <i class="el-icon-top-right"></i>
            <span>{{item.financing|financing}}</span>
          </div>
          <div class="icio">
            <i class="el-icon-location-outline"></i>
            <span>北京市</span>
          </div>
        </div>
      </div>

      <div class="page">
        <el-pagination
          background
          :current-page.sync="currentPage1"
          :page-size="10"
          layout="prev, pager, next, jumper"
          :total="response.total"
        ></el-pagination>
      </div>
    </div>

    <div class="fourzerofour" v-show="fourzerofour" >
      <div class="fourzerofour_row1">
        <img src="../assets/404.png" alt style="width:150px" />
        <div class="tips">
          <h4>暂时没有符合该搜索条件的内容</h4>
          <p>请重新修改搜索条件后再次进行搜索</p>
        </div>
      </div>
      <h5>推荐公司</h5>
      <div class="fourzerofour_row2">
        <div class="company" v-for="(item,key) in tuijian " @click="gslb(item.id)">
          <div class="companyintroduce">
            <div class="img">
              <img :src="item.logo" alt />
            </div>
            <div class="text">
              <p class="p1">{{item.name}}</p>
              <p class="p2">{{item.slogan}}</p>
              <p class="p3">
                <span>{{item.professionNum}}</span>个再招职位
              </p>
            </div>
          </div>
          <div class="companyindustry">
            <div class="icio">
              <i class="el-icon-mobile-phone"></i>
              <span v-for="ite in item.industryList">{{ite|industry}}</span>
            </div>
            <div class="icio">
              <i class="el-icon-top-right"></i>
              <span>{{item.financing|financing}}</span>
            </div>
            <div class="icio">
              <i class="el-icon-location-outline"></i>
              <span>北京市</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "componentslist",
  data() {
    name: "companylist";
    return {
      region: [
        { option: "不限", Choice: true },
        { option: "北京", Choice: false }
      ],
      industry: [
        { option: "不限", Choice: true },
        { option: "移动互联网", Choice: false },
        { option: "电子商务", Choice: false },
        { option: "企业服务", Choice: false },
        { option: "O2O", Choice: false },
        { option: "教育", Choice: false },
        { option: "金融", Choice: false },
        { option: "游戏", Choice: false }
      ],
      financing: [
        { option: "不限", Choice: true },
        { option: "无需融资", Choice: false },
        { option: "天使轮", Choice: false },
        { option: "A轮", Choice: false },
        { option: "B轮", Choice: false },
        { option: "C轮", Choice: false },
        { option: "D轮及以上", Choice: false },
        { option: "上市公司", Choice: false }
      ],
      name: "",
      api: "/api/a/company/search?",
      currentPage1: 1,
      response: [],
      total: 10,
      fourzerofour: false,
      tuijian: null
    };
  },
  filters: {
    industry: function(key) {
      let o;
      switch (key) {
        case 0:
          o = "移动互联网";

          break;
        case 1:
          o = "电子商务";

          break;
        case 2:
          o = "企业服务";

          break;
        case 3:
          o = "O2O";

          break;
        case 4:
          o = "教育";

          break;
        case 5:
          o = "金融";

          break;
        case 6:
          o = "游戏";

          break;
      }
      return o;
    },
    financing: function(key) {
      let o;
      switch (key) {
        case 0:
          o = "无需融资 ";

          break;
        case 1:
          o = "天使轮";

          break;
        case 2:
          o = "A轮";

          break;
        case 3:
          o = "B轮";

          break;
        case 4:
          o = "C轮";

          break;
        case 5:
          o = "D轮及以上";

          break;
        case 6:
          o = "上市公司";

          break;
      }
      return o;
    }
  },
  created() {
    if (this.$route.query.name) {
      this.name = this.$route.query.name;
      this.api += "name=" + this.name + "&";
      sessionStorage.clear();
    }
    this.api += "size=9&page=";

    if (sessionStorage.getItem("api") != null) {

        if (sessionStorage.getItem("api").indexOf('profession') != -1) {
              sessionStorage.clear();
          }else{
               this.api = sessionStorage.getItem("api");
          }
    }
    if (sessionStorage.getItem("currentPage1") != null) {
      this.currentPage1 = Number(sessionStorage.getItem("currentPage1"));
    }
    if (sessionStorage.getItem("name") != null) {
      this.name = sessionStorage.getItem("name");
    }

    if (sessionStorage.getItem("region") != null) {
      this.region = JSON.parse(sessionStorage.getItem("region"));
    }
    if (sessionStorage.getItem("industry") != null) {
      this.industry = JSON.parse(sessionStorage.getItem("industry"));
    }
    if (sessionStorage.getItem("financing") != null) {
      this.financing = JSON.parse(sessionStorage.getItem("financing"));
    }
    sessionStorage.setItem("api", this.api);
    axios.get(this.api + this.currentPage1).then(response => {
      this.response = response.data;
      this.fourzerofour = false;
      console.log(this.response)
    });
  },
  methods: {
    dianji(index, array) {
      //    if(index==0){
      //         for(let i =1;i<array.length;i++){
      //            array[i].Choice=false
      //         }
      //            array[0].Choice=true
      //    }else{

      //     array[index].Choice=!array[index].Choice
      //     array[0].Choice =  array.every(function(element){
      //        if(element.Choice===true){
      //                    return false
      //                 }
      //                return true
      //           })
      //    }
      this.$service.search_criteria(index, array);
    },
    search() {
      let api = "/api/a/company/search?";
      //  搜索关键字
      if (this.name) {
        api += `name=${this.name}&`;
        sessionStorage.setItem("name", this.name);
      }
      // 地区搜索
      for (let i = 1; i < this.region.length; i++) {
        if (this.region[i].Choice == true) {
          api = api + "city=" + i + "&";
        }
      }
      sessionStorage.setItem("region", JSON.stringify(this.region));
      // 行业搜索
      for (let i = 1; i < this.industry.length; i++) {
        if (this.industry[i].Choice == true) {
          api = api + "industry=" + (i - 1) + "&";
        }
      }
      sessionStorage.setItem("industry", JSON.stringify(this.industry));
      // 融资搜索
      for (let i = 1; i < this.financing.length; i++) {
        if (this.financing[i].Choice == true) {
          api = api + "financing=" + (i - 1) + "&";
        }
      }
      sessionStorage.setItem("financing", JSON.stringify(this.financing));
      api += "size=9&page=";
      this.currentPage1 = 1;

      sessionStorage.setItem("api", api);
      axios.get(api + this.currentPage1).then(response => {
        this.response = response.data;
        this.fourzerofour = false;
        this.$router.push({ path: "/searchmain/companylist" });
      });
    },
    empty() {
      sessionStorage.clear();
      this.name = "";
     console.log(111)
      for (let i = 1; i < this.region.length; i++) {
        this.region[i].Choice = false;
      }
      this.region[0].Choice = true;
      for (let i = 1; i < this.industry.length; i++) {
        this.industry[i].Choice = false;
      }
      this.industry[0].Choice = true;
      for (let i = 1; i < this.financing.length; i++) {
        this.financing[i].Choice = false;
      }
      
      this.financing[0].Choice = true;
      this.api = "/api/a/company/search?size=9&page=";
      this.currentPage1 = 1;
     console.log(222)
      sessionStorage.setItem("api", this.api);
      sessionStorage.setItem("currentPage1", this.currentPage1);
      console.log(333)
      axios.get(this.api + this.currentPage1).then(response => {
        this.response = response.data;
        this.fourzerofour = false;
        this.$router.push({ path: "/searchmain/companylist" });
        console.log(444)
      });
      console.log(555)
    },
    gslb(id){
         this.$router.push({path:`/company?id=${id}`})
    },
   
  },
  watch: {
    currentPage1: function(newvalue, oldvalue) {
      console.log(666)
      this.api = sessionStorage.getItem("api");
     
     
      axios.get(this.api + this.currentPage1).then(response => {
        this.response = response.data;
        // sessionStorage.setItem("api", this.api)
        sessionStorage.setItem("currentPage1", this.currentPage1);
         this.$router.push({ path: "/searchmain/companylist" });
        // this.fourzerofour=false
        
      });
    },

    response: function(newvalue, oldvalue) {

      if (this.response.data) {
        if (this.response.data.length == 0) {
          axios.get("/api/a/company/search?size=3").then(response => {
            this.tuijian = response.data.data;
            console.log(this.tuijian);
            this.fourzerofour = true;
          });
        }
      }
    }
  }
};
</script>
