        .p2,
        .p3 {
          margin: 10px 20px;
        }
        .p1 {
          font-size: 13px;
          font-weight: 900;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
        .p2 {
          font-size: 11px;
          color: rgb(83, 106, 130);
          margin-right: 5px;
          white-space: nowrap;
