          overflow: hidden;
        }
        .p3 {
          font-size: 11px;
          color: rgb(157, 166, 175);
          span {
            color: red;
            font-weight: 700;
          }
        }
      }
    }
    .companyindustry {
      height: 30px;
      background: rgb(250, 250, 250);
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      display: flex;
      align-items: center;
      .icio {
        width: 30%;
        font-size: 10px;
        color: rgb(157, 166, 175);
        padding-left: 10px;
        margin-right: 20px;
        text-align: center;
        margin-right: 5px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        span {
          margin: 0 3px;
          white-space: nowrap;
