          overflow: hidden;
        }
      }
    }
  }
  @media (max-width: 768px) {
    .company {
      width: 48%;
      margin-right: 1%;
    }
  }
  @media (max-width: 650px) {
    .company {
      width: 100%;
    }
  }
  .page {
    width: 100%;
    height: 30px;
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}

