   <p class="jieshao">您将感兴趣的职位、公司名称以及您的个人信息发送到我们的邮箱，我们会尽快帮您安排。</p>
   <p class="jieshao">邮箱地址：hr@ptteng.com</p>
</el-dialog>
      </div>
