<script>
import axios from 'axios';
export default {
  name: 'foot',
    data(){
   return{
       dialogVisible: false,
       data:null,
       requisite:null,
       responsibility:null
    }
},  filters: {
      formatDate: function (value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' ;
      },
      industry:function(key){
        let o;
        switch(key){
             case 0:
             o = '移动互联网';
        
             break;
             case 1:
             o = '电子商务';
          
             break;
             case 2:
             o = '企业服务';
         
             break;
             case 3:
             o = 'O2O';
           
             break;
             case 4:
             o = '教育';
         
             break;
             case 5:
             o = '金融';
         
             break;
             case 6:
             o = '游戏';
         
             break
             
        }
        return o
      }
    },
  created(){
  console.log(this.$route.query)
//   职位详细信息
    axios.get(`/api/a/profession/${this.$route.query.id}`).then((response)=>{
     this.data=response.data.data
     console.log(this.data)
     let str=response.data.data.requisite.replace(/\n/g,"*")
     let yu=response.data.data.responsibility.replace(/\n/g,"*")
     str=str.split("*")
       yu=yu.split('*')
       for(let i=0;i<str.length;i++){
         if(str[i]==""){
           str.splice(i,1)
           i--
         }
         if(yu[i]==""){
           yu.splice(i,1)
           i--
         }
     }
      console.log(str)
    this.requisite=str
    this.responsibility=yu


   
    
});

},
  methods:{
  
  
  },
  components: {
   
  }
}
</script>


