<script>
import axios from 'axios';
let recommend;
let newest;
export default {
    name:'Fandjob',
  data(){
   return{
    show: true,
    lo:false,
    top:'54px',
    banner:null,
    recommend:null,
    newest:null,
    job:null,
    company:null,
    wheelcompany:[],
    zuixintuijian:true,
    name:''
    }
 },
 
  created(){
    sessionStorage.clear()
    // 请求轮播图
    
 axios.get('/api/a/article/search').then((response)=>{
   let u=[response.data.data.articleList[2],response.data.data.articleList[3]]
  this.banner=u
  console.log(response.data.data.articleList)
});
// 请求推荐职业
 axios.get('/api/a/profession/search?recommend=1 ').then((response)=>{
   recommend=response.data.data
   this.job=recommend
  
});

// 请求最新职业
axios.get('/api/a/profession/search?recommend=0 ').then((response)=>{
    newest=response.data.data
      for(let i=0;i<4;i++){
     this.wheelcompany.push(response.data.data[i])
   }
  console.log(response.data.data)
   console.log(this.wheelcompany)
});
// 获取公司信息
axios.get('/api/a/company/search').then((response)=>{
   this.company=response.data.data
  console.log(response)
  console.log(response.data.data)
   
});

  },methods:{
  
  rec(){
this.job=recommend
this.zuixintuijian=true
  },
  ne(){
  this.job=newest
  this.zuixintuijian=false
  },
  ckgd(){
      this.$router.push({path:`joblist`})
  },
   job2(key){
    console.log(key)
     this.$router.push({path:`jobdetails?id=${key}`})
   },
   company2(id){
    console.log(id)
     this.$router.push({path:`company?id=${id}`})
   },
   searchmain(){
   this.$router.push({path:'searchmain/companylist'})
   },
   search(){
     this.$router.push({path:`searchmain/companylist?name=${this.name}`})
   }
 },
 
}
</script>
